import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
  selector: 'bb-loans-heading',
  templateUrl: './loans-heading.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LoansHeadingComponent {
  /**
   * Heading title
   */
  @Input() headingTitle!: string;

  /**
   * Heading main button title
   */
  @Input() mainButtonTitle!: string;

  /**
   * Flag indicates main button visibility
   */
  @Input() showMainButton!: boolean;

  /**
   * Flag indicates navigation button visibility
   */
  @Input() showNavigationButton!: boolean | null;

  /**
   * Heading navigation button title
   */
  @Input() navigationButtonTitle!: string;

  /**
   * Heading navigation button press action
   */
  @Output() navigationButtonAction = new EventEmitter<void>();

  /**
   * Heading main button press action
   */
  @Output() mainButtonAction = new EventEmitter<void>();
}
